import cookie from 'cookie'
import utils from '~/common/utils'
import TopicsNav from '~/components/TopicsNav'
import CommonTopicList from '~/components/CommonTopicList'

// import TweetsWidget from '~/components/TweetsWidget'
// import SiteNotice from '~/components/SiteNotice'
// import ScoreRank from '~/components/ScoreRank'
// import FriendLinks from '~/components/FriendLinks'
// // import Pagination from '~/components/Pagination'

export default {
  components: {
    // SiteNotice,
    // ScoreRank,
    // FriendLinks,
    // TweetsWidget,
    CommonTopicList
  },
  async asyncData({ $axios, query, req }) {
    let city = '北京'
    if ($axios.defaults.headers.common.cookie) {
      const cookies = cookie.parse($axios.defaults.headers.common.cookie || '')
      if (cookies && cookies !== '') {
        city = cookies.city
      }
    } else {
      city = utils.getCity()
    }
    try {
      const [topicsPage] = await Promise.all([
        $axios.get('/api/topic/node/topics', {
          params: {
            page: query.p || 1,
            city,
            nodeId: 3
          }
        })
      ])
      return { topicsPage }
    } catch (e) {
      console.error(e)
    }
  },
  data() {
    return {
      currentCity: ''
    }
  },
  mounted() {
    this.currentCity = utils.getCity()
  },
  methods: {
    twitterCreated(data) {
      if (this.topicsPage) {
        if (this.topicsPage.results) {
          this.topicsPage.results.unshift(data)
        } else {
          this.topicsPage.results = [data]
        }
      }
    }
  },
  head() {
    return {
      title: this.$siteTitle('90社区—周末去哪儿玩'),
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: this.$siteDescription()
        },
        { hid: 'keywords', name: 'keywords', content: this.$siteKeywords() }
      ]
    }
  }
}
